*{
  box-sizing: border-box;
  margin:0;
  padding:0;
}

.app{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top:100px;
  
}

.app-container{
  width:30%;
  
}

header{
  background-color: #ccc;
  border-radius: 5px 5px 0px 0px;
  height:50px;
  display: flex;
  align-items: center;
  padding-left:10px;
  font-size: 1rem;
  font-weight: 700;
  color: #333;
}

input{
  height:30px;
  width:100%;
  outline: none;
  border: 1px solid #ccc;
  padding:10px;
}

.friends{
  list-style-type: none;
}

.friends li{
  border:1px solid #ccc;
  width:100%;
  height:50px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding:10px;
  flex-grow: 3 2 2;
}

.friends li div{
  width:100%;
  text-align: left;
}

.friends li div .name{
  font-weight: 700;
  font-size:1rem;
  color:#333;
}

.friends li div .description{
  font-size:0.8rem;
  color:#777;
}

i{
  margin:5px;
  cursor: pointer;
  border: 1px solid #ccc;
  padding:5px;
  border-radius: 5px;
}

i:hover{
  transform: scale(1.2);
}

.page{
  display: flex;
  justify-content: flex-end;
  list-style-type: none;
  
}

.page li{
  margin:5px;
  cursor: pointer;
  font-size:0.8rem;
  font-weight:700;
  color:#444;
  border: 1px solid #ccc;
  padding:0 5px 0 5px;
  border-radius: 5px;
  
}

.page li:hover{
  transform: scale(1.2);
  color: blue;
}

@media only screen and (max-width: 600px) {
  .friends li{
    border:1px solid #ccc;
    width:100%;
    height:auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:10px;
    flex-grow: 1 1 1;
  }
  
  .friends li div{
    width:100%;
    text-align: center;
  }

  header{
    background-color: #ccc;
    border-radius: 5px 5px 0px 0px;
    min-height: 50px;
    height:auto;
    display: flex;
    align-items: center;
    padding-left:10px;
    font-size: 1rem;
    font-weight: 700;
    color: #333;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
}
